import React,{Component} from 'react';
import "./logbar.css";
import {withRouter} from 'react-router';
import {connect} from 'react-redux';
import {My_pings} from './redux/ActionCreator';


const mapStateToProps=state=>{
    return {
    pings:state.pings,
}}

class logbar extends Component{
    remove_loc=(event)=>{
        event.target.style.backgroundColor="red";
        fetch('/delete/location',{
            method:'delete',
            body:JSON.stringify({
                title:event.target.innerHTML,
            }),
            headers:{
                'Content-Type':"application/json",
                'credential':"self-origin",
            }
        })
        .then(response=>response.json())
        .then(data=>{
            this.props.My_pings(data);
        })
    }
    render(){
        var data=[];
        for(var i=0;i<this.props.pings.length;i+=2){
            if(this.props.ping[i]){
            data.push(
            <li onClick={this.remove_loc}>{(this.props.pings[i].title).slice(71,(this.props.pings[i].title).length-4)}</li>
            )
            }
        }
        return(
            <div className="sidebar-container side-bar-right">
                <div className="ping-log-info">Ping Logs</div>
            <ul className="log-bar">
               {data}
            </ul>
            </div>
        )
    }
}
export default withRouter(connect(mapStateToProps,null)(logbar));